.SlideAnimation
{
    animation: SlideIt 15s infinite  ;
    animation-delay: 3s;
    animation-timing-function:step-start;
}




@keyframes SlideIt {
    0%{
        left:0%;
    }
    20%
    {
        left: -100%;
    }
    40%{
        left:-200%;
    }
    60%
    {
        left:-300%;
    }
    80%
    {
        left:-400%;
    }
    100%{
        left:-500%;
    }
    
    
    
}